import { createElement } from 'react';
import { createParser } from './createParser';
import { mergeDescriptors, resolveComponent, stringifyPath } from './utils';

export const createDynamicRenderer = ({
  ast,
  root = 'MAIN',
  render,
  children = ({ render }) => render(),
  Component,
  resolver,
  cacheKey = 'component',
  refKey = '$ref',
  merge = mergeDescriptors,
  interpolate = /\{\{(.+?)\}\}/g,
  context = {},
  contextKey = '$'
}) => {
  const parse = createParser(
    (...args) => fn(...args),
    refKey,
    context,
    contextKey,
    ast,
    interpolate,
    merge
  );

  const fn = (resolved, path = [root]) => {
    const key = stringifyPath(path);

    if (!resolved)
      throw new Error(`Failed to resolved component found at path ${key}`);

    const props = {
      descriptor: resolved,
      key,
      render: (innerProps = {}, descriptor = resolved) => {
        const final = parse({
          ...descriptor,
          props: {
            key,
            children: descriptor.children,
            ...descriptor.props,
            ...innerProps
          },
          key
        });

        return createElement(
          resolveComponent(resolver, final, cacheKey, key),
          final.props
        );
      }
    };

    return Component
      ? createElement(Component, props)
      : (render || children)(props);
  };

  return fn(ast[root]);
};
