import { traverse, get, stringifyPath } from './utils';

const createVarParser = (pattern, context, ast, contextKey, again) => (
  descriptor,
  path
) =>
  traverse(
    descriptor,
    (v) => pattern.test(v),
    (v, path) =>
      again(
        v.replace(pattern, (...args) => {
          const v = args[1];
          const o = v[0] === contextKey ? context : ast;
          const r = get(o, o === context ? v.substr(1) : v);
          if (r === undefined)
            throw new Error(
              `Could not perform interpolation of ${
                args[0]
              } at path ${stringifyPath(path)}`
            );

          return r;
        })
      ),
    path
  );

const createRefParser = (dynamicCreator, refKey, merge, ast) => (
  descriptor,
  path
) =>
  traverse(
    descriptor,
    (v) => v && v[refKey],
    (r, path) => {
      const { ref, rest = {} } = Object.entries(r).reduce((acc, [k, v]) => {
        const isKey = k === refKey;
        return {
          ref: isKey ? v : acc.ref,
          rest: isKey ? acc.rest : { ...acc.rest, [k]: v }
        };
      }, {});
      const isStringRef = typeof ref === 'string';
      const resolved = isStringRef ? get(ast, ref) : ref;
      const nextPath = [...path, isStringRef ? ref : refKey];
      if (!resolved)
        throw new Error(
          `Unable to resolve ref at path ${stringifyPath(nextPath)}`
        );

      return dynamicCreator(merge(resolved, rest), nextPath);
    },
    path
  );

// parsers traverse descriptor objects and modify them
export const createParser = (
  dynamicCreator,
  refKey,
  context,
  contextKey,
  ast,
  pattern,
  merge
) => {
  const varParser = createVarParser(
    pattern,
    context,
    ast,
    contextKey,
    (...args) =>
      createParser(
        dynamicCreator,
        refKey,
        context,
        contextKey,
        ast,
        pattern,
        merge
      )(...args)
  );
  const refParser = createRefParser(dynamicCreator, refKey, merge, ast);
  return (descriptor, path) => refParser(varParser(descriptor, path), path);
};
